<template>
  <div class="posts flex-row pd-1">
    <div class="posts-left flex-column">
      <button class="pure-button">
        <router-link to="/add/post">
          发帖
        </router-link>
      </button>
      <!-- 
        TODO:
        avoid hard codes.
       -->
      <ul class="mt-5 post-category-list" @click="toCategory">
        <li data-category="">
          <span class="category-label category-label--all"></span>
          所有
        </li>
        <li data-category="general">
          <span class="category-label category-label--general"></span>
          一般
        </li>
        <li data-category="advice">
          <span class="category-label category-label--advice"></span>
          建议
        </li>
        <li data-category="bugs">
          <span class="category-label category-label--bug"></span>
          bugs
        </li>
        <li data-category="help">
          <span class="category-label category-label--help"></span>
          帮助
        </li>
      </ul>
    </div>
    <div class="posts-right ml-1 flex-1 flex-column relative" v-loading="loading">
      <div class="flex-row pd-1">
        <input type="text" class="pure-input ml-auto" id="posts-search" placeholder="Search">
        <button class="ml-1 pure-button pure-button-primary" @click.prevent="toSearch">搜索</button>
      </div>
      <no-data class="flex-1" v-if="posts.length === 0 && !loading"></no-data>
      <ul v-else
        class="flex-1">
        <li
        v-for="post in posts"
        :key="post.title"
        >
        <post-card 
        :post="post"
        />
        </li>
      </ul>
      <pager 
      :len="pager.limit"
      :active="pager.page"
      :total="pager.total"
      @click="toPage"
      />
    </div>
  </div>
</template>

<script>
import PostCard from './components/post-card.vue';

export default {
  components: {
    PostCard,
  },
  data() {
    return {
      posts: [],
      loading: false,
      category: '',
      keyword: '',

      pager: {
        page: 1,
        limit: 10,
        total: 0,
      }
    }
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      if (this.loading) {
        return;
      }
      this.loading = true;
      this.$axios.get(`${this.$url.posts}?category=${this.category}&keyword=${this.keyword}&page=${this.pager.page}&limit=${this.pager.limit}`)
      .then((resp) => {
        this.posts = resp.data.results;
        this.pager.total = resp.data.total;
        this.loading = false;
      })
    },
    toPage(index) {
      this.pager.page = index;
      this.getData();
    },
    toSearch() {
      const el = document.getElementById('posts-search');
      const keyword = el.value.trim();
      this.keyword = keyword;
      this.getData();
    },
    toCategory(event) {
      const category = event.target.getAttribute('data-category');
      this.category = category;
      this.getData();
    }
  }
}
</script>

<style lang="scss" scoped>
.posts {
  margin: 2em;
  .posts-left {
    width: 25%;
  }
  .category-label {
    display: inline-block;
    width: 1em;
    height: 1em;;
    border-radius: 0.2em;
  }
  .category-label--all {
    background: silver;
  }
  .category-label--general {
    background: $primary;
  }
  .category-label--bug {
    background: $error;
  }
  .category-label--help {
    background: $success;
  }
  .category-label--advice {
    background: $warning;
  }
  .post-category-list {
    li {
      transition: all 0.3s;
      font-size: 0.8em;
      color: #777;
      padding: 0.6em 0;
      &:hover {
        color: #333;
        cursor: pointer;
      }
    }
  }
}

</style>
