<template>
  <div class="post-card" @click="toPost">
    <img 
    class="post-card__avatar"
    src="/logo.png"
    />
    <div class="post-card__body">
      {{ post.title }}
    </div>
    <div class="post-card__meta">
      <span class="post-label" :class="'post-label--' + post.category">
      {{ post.category }}
      </span>
      <i class="ml-1 far fa-comment">
        {{ post.replys }}
      </i>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    post: {
      type: Object,
      required: true,
    }
  },
  methods: {
    toPost() {
      this.$router.push(`/posts/${this.post.title}`);
    }
  }
}
</script>

<style lang="scss" scoped>
.post-label {
  display: inline-block;
  padding: 0.2em 0.6em;
  color: white;
  border-radius: 0.4em;
  font-size: 0.8em;
} 
.post-label--general {
  background: $primary;
}
.post-label--bug {
  background: $error;
}
.post-label--advice {
  background: $warning;
}
.post-label--help {
  background: $success;
}

.post-card {
  display: flex;
  flex-direction: row;
  transition: all 0.3s;
  padding: 0.4em 0.6em;
  &:hover {
    background: #11111111;
    
    cursor: pointer;
  }
  .post-card__avatar {
    width: 50px;
    height: 50px;
    border-radius: 50px;
  }
  .post-card__body {
    flex: 1;
  }
}
</style>
